@file:OptIn(ExperimentalResourceApi::class, ExperimentalMaterial3Api::class)

package de.labinger.holding

import DebugScreen
import androidx.compose.foundation.clickable
import androidx.compose.foundation.layout.*
import androidx.compose.material.icons.Icons
import androidx.compose.material.icons.filled.Home
import androidx.compose.material.icons.filled.Info
import androidx.compose.material.icons.filled.Refresh
import androidx.compose.material3.*
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import androidx.lifecycle.viewmodel.compose.viewModel
import de.labinger.holding.ble.PermissionRequestPanel
import de.labinger.holding.ble.command.LedMode
import de.labinger.holding.theme.AppTheme
import org.jetbrains.compose.resources.ExperimentalResourceApi
import org.jetbrains.compose.ui.tooling.preview.Preview

enum class Destination {
    MAIN,
    DEBUG
}

@Preview
@Composable
fun MainScreen(
    viewModel: MainViewModel = viewModel { MainViewModel() }
) {
    AppTheme(true) {
        Surface {
            var destination by remember { mutableStateOf(Destination.MAIN) }
            Scaffold(
                bottomBar = { MainBottomAppBar(onActionClick = { d -> destination = d} ) },
                content = { paddingValues ->
                    when(destination) {
                        Destination.MAIN -> MainContent(paddingValues, viewModel)
                        Destination.DEBUG -> DebugScreen()
                    }
                }
            )
        }
    }
}

@Composable
private fun MainContent(
    paddingValues: PaddingValues,
    viewModel: MainViewModel
) {
    Column(
        modifier = Modifier.fillMaxSize().padding(paddingValues),
        horizontalAlignment = Alignment.CenterHorizontally,
        verticalArrangement = Arrangement.spacedBy(16.dp)
    ) {
        when (val state =
            viewModel.stateFlow.collectAsState(MainUiState.Progress("initial")).value) {
            is MainUiState.MissingPermissions -> PermissionRequestPanel(
                state.permissions,
                state.reevaluatePermissions
            )

            MainUiState.PermissionsGranted -> ProgressInfo()
            is MainUiState.RequestAccess -> ConnectButton(state.onClickRequest)
            MainUiState.Error -> ErrorRetryPanel(onClick = viewModel::retry)
            is MainUiState.Progress -> ProgressInfo(message = state.message)
            is MainUiState.Ready -> ControlPanel(state = state)
        }
    }
}

@Composable
private fun MainBottomAppBar(
    onActionClick: (Destination) -> Unit = {}
) {
    BottomAppBar(
        actions = {
            IconButton(onClick = {onActionClick(Destination.MAIN)}) {
                Icon(
                    imageVector = Icons.Default.Home,
                    contentDescription = "Home"
                )
            }
            IconButton(onClick = {onActionClick(Destination.DEBUG)}) {
                Icon(
                    imageVector = Icons.Default.Info,
                    contentDescription = "Refresh"
                )
            }
        }
    )
}

@Composable
fun ConnectButton(onClickRequest: () -> Unit) {
    Box(modifier = Modifier.fillMaxSize(), contentAlignment = Alignment.Center) {
        Button(onClickRequest) {
            Text("Connect")
        }
    }
}

@Composable
private fun ControlPanel(
    state: MainUiState.Ready
) {
    ModeSelectionGroup(
        onClickRefresh = state.onClickRefresh,
        modes = state.modes,
        initialModeIndex = state.initialModeIndex,
    )
    StateSlider(
        title = "Brightness",
        initialValue = state.initialBrightnessValue,
        onBrightnessChange = state.onBrightnessChange
    )
    StateSlider(
        title = "Speed",
        initialValue = state.initialSpeedValue,
        onBrightnessChange = state.onSpeedChange
    )
}

@Composable
private fun StateSlider(
    title: String,
    initialValue: Float,
    onBrightnessChange: (Float) -> Unit
) {
    Card {
        Column(
            modifier = Modifier.padding(8.dp)
        ) {
            Text(
                text = title,
                style = MaterialTheme.typography.titleMedium
            )
            var value by remember { mutableStateOf(initialValue) }
            Slider(value = value, onValueChange = { newValue ->
                value = newValue
                onBrightnessChange(newValue)
            })
        }
    }
}

@Composable
private fun ModeSelectionGroup(
    modes: List<LedMode>,
    initialModeIndex: Int,
    onClickRefresh: () -> Unit,
) {
    Card {
        Column(
            modifier = Modifier.padding(8.dp)
        ) {
            RefreshTitleBar(onClickRefresh)
            var selectedIndex by remember { mutableStateOf(initialModeIndex) }
            modes.forEachIndexed { index, ledMode ->
                ModeSelection(
                    modeName = ledMode.name,
                    selected = index == selectedIndex,
                    onSelect = {
                        selectedIndex = index
                        ledMode.onSelect()
                    }
                )
            }
        }
    }
}

@Composable
private fun RefreshTitleBar(onClickRefresh: () -> Unit) {
    Row(
        modifier = Modifier.fillMaxWidth(),
        horizontalArrangement = Arrangement.SpaceBetween,
        verticalAlignment = Alignment.CenterVertically,
    ) {
        Text(
            text = "LED mode selection",
            style = MaterialTheme.typography.titleMedium
        )
        IconButton(onClick = onClickRefresh) {
            Icon(
                imageVector = Icons.Default.Refresh,
                contentDescription = "Refresh"
            )
        }
    }
}

@Composable
private fun ModeSelection(
    modeName: String,
    selected: Boolean,
    onSelect: () -> Unit
) {
    Column {
        Row(
            verticalAlignment = Alignment.CenterVertically,
            modifier = Modifier
                .fillMaxWidth()
                .clickable(onClick = onSelect),
        ) {
            RadioButton(
                selected = selected,
                onClick = onSelect
            )
            Text(text = modeName, style = MaterialTheme.typography.bodyMedium)
        }
        Divider(Modifier.fillMaxWidth())
    }
}

@Composable
fun ProgressInfo(
    message: String? = null
) {
    Column(
        modifier = Modifier.fillMaxSize(),
        horizontalAlignment = Alignment.CenterHorizontally,
        verticalArrangement = Arrangement.Center
    ) {
        if (message != null) {
            Text(text = message)
        }
        LinearProgressIndicator()
    }
}

@Composable
fun ErrorRetryPanel(
    onClick: () -> Unit
) {
    Column(
        modifier = Modifier.fillMaxSize(),
        horizontalAlignment = Alignment.CenterHorizontally,
        verticalArrangement = Arrangement.Center
    ) {
        Text("Error during BT scanning")
        Button(onClick = onClick) {
            Text("Retry")
        }
    }
}