@file:Suppress("NOTHING_TO_INLINE")

package de.labinger.holding.utils

import androidx.compose.runtime.Immutable
import co.touchlab.kermit.Logger
import kotlinx.coroutines.flow.MutableStateFlow

@Immutable
data class LogEntries(
    val entries: List<LogEntry>
)

@Immutable
data class LogEntry(
    val level: String,
    val message: String
)

var logEntries = mutableListOf<LogEntry>()
val logStateFlow = MutableStateFlow(LogEntries(emptyList()))

inline fun loge(message: String, throwable: Throwable? = null) {
    Logger.e(message, throwable, "Labinger")
    logEntries.add(LogEntry("E", message + "\n" + throwable?.stackTraceToString()))
    logStateFlow.value = LogEntries(logEntries)
}

inline fun logd(message: String, throwable: Throwable? = null) {
    Logger.d(message, throwable, "Labinger")
    logEntries.add(LogEntry("D", message + "\n" + throwable?.stackTraceToString()))
    logStateFlow.value = LogEntries(logEntries)
}

inline fun logi(message: String, throwable: Throwable? = null) {
    Logger.i(message, throwable, "Labinger")
    logEntries.add(LogEntry("I", message + "\n" + throwable?.stackTraceToString()))
    logStateFlow.value = LogEntries(logEntries)
}