package de.labinger.holding

import RestartableStateFlow
import androidx.compose.runtime.Immutable
import androidx.lifecycle.ViewModel
import androidx.lifecycle.viewModelScope
import de.labinger.holding.ble.BleFinderResult
import de.labinger.holding.ble.determineMissingBlePermissions
import de.labinger.holding.ble.findBlePeripheral
import de.labinger.holding.ble.command.LedMode
import de.labinger.holding.ble.command.BleCommand
import de.labinger.holding.ble.command.BleCommand.Companion.createBrightnessCommand
import de.labinger.holding.ble.command.BleCommand.Companion.createModeCommand
import de.labinger.holding.ble.command.BleCommand.Companion.createSpeedCommand
import de.labinger.holding.ble.command.createLedModes
import com.benasher44.uuid.uuidFrom
import com.juul.kable.Peripheral
import de.labinger.holding.utils.loge
import kotlinx.coroutines.*
import kotlinx.coroutines.flow.*
import restartableStateIn

class MainViewModel: ViewModel() {
    private val permissionStateFlow = MutableStateFlow(createPermissionState())
    private val pendingCommandSendChannel = MutableStateFlow<suspend () -> Unit> {}

    val stateFlow: RestartableStateFlow<MainUiState> = permissionStateFlow.transform { state ->
        emit(state)
        if (state != MainUiState.PermissionsGranted) {
            return@transform
        }
        emit(MainUiState.Progress("Initializing..."))
        val uuids = listOf(uuidFrom("4fafc201-1fb5-459e-8fcc-c5c9c331914b"))
        val bleFlow = findBlePeripheral(viewModelScope, "MyESP32", uuids).transform { bleState ->
            val mainState = when(bleState) {
                is BleFinderResult.Request -> MainUiState.RequestAccess(bleState.onClick)
                is BleFinderResult.Progress -> MainUiState.Progress(bleState.message)
                is BleFinderResult.Success -> {

                    val peripheral = bleState.peripheral
                    addCloseable( AutoCloseable { peripheral.disconnectSafely() })
                    peripheral.connectSafely()

                    val services = peripheral.services ?: error("No services available")

                    val service = services.firstOrNull {
                        it.serviceUuid == uuidFrom("4fafc201-1fb5-459e-8fcc-c5c9c331914b")
                    } ?: error("Could not find service UUID")

                    val char = service.characteristics.firstOrNull {
                        it.characteristicUuid == uuidFrom("beb5483e-36e1-4688-b7f5-ea07361b26a8")
                    } ?: error("Could not find characteristic UUID")

                    val writer: suspend (ByteArray) -> Unit = { data ->
                        peripheral.write(char, data)
                    }

                    MainUiState.Ready(
                        initialModeIndex = 0,
                        modes = createLedModes { sendMode(writer, it) },
                        initialSpeedValue = 1f,
                        initialBrightnessValue = 0.2f,
                        onBrightnessChange = { sendBrightness(writer, it) },
                        onSpeedChange = { sendSpeed(writer, it) },
                        onClickRefresh = {
                            viewModelScope.launch {
                                peripheral.disconnect()
                                retry()
                            }
                        },
                    )
                }
            }
            emit(mainState)
        }
        emitAll(bleFlow)
    }
    .catch {
        loge( "Error creating MainUiState ${it::class}", it)
        emit(MainUiState.Error)
    }
    .restartableStateIn(
        scope = viewModelScope,
        started = SharingStarted.WhileSubscribed(5000),
        initialValue = MainUiState.Progress("Initializing...")
    )

    init {
        viewModelScope.launch {
            pendingCommandSendChannel.collectLatest { writeAction ->
                try {
                    delay(50) // Prevents flooding
                    writeAction()
                } catch (e: Exception) {
                    loge("Error writing to gatt", e)
                }
            }
        }
    }

    private fun createPermissionState(): MainUiState {
        val missingPermissions = determineMissingBlePermissions()
        val state = if (missingPermissions.isEmpty()) {
            MainUiState.PermissionsGranted
        } else {
            MainUiState.MissingPermissions(
                permissions = missingPermissions,
                reevaluatePermissions = { permissionStateFlow.value = createPermissionState() }
            )
        }
        return state
    }

    private fun sendSpeed(writer: suspend (ByteArray)-> Unit, speed: Float) {
//        val attenuatedSpeed = ln(speed * 255.0) / ln(255.0) * 255
        val attenuatedSpeed = speed * speed * speed
        val byteSpeed = (attenuatedSpeed * 255).toInt().toUByte()
        val command = createSpeedCommand(byteSpeed)
        dispatch(writer, command)
    }

    private fun sendBrightness(writer: suspend (ByteArray)-> Unit, value: Float) {
//        val attenuatedBrightness = ln(value * 255.0) / ln(255.0) * 255
        val attenuatedBrightness = value * value * value
        val byteBrightness = (attenuatedBrightness * 255).toInt().toUByte()
        val command = createBrightnessCommand(byteBrightness)
        dispatch(writer, command)
    }

    private fun sendMode(writer: suspend (ByteArray) -> Unit, mode: Int) {
        dispatch(writer, createModeCommand(mode))
    }

    private fun dispatch(writer: suspend (ByteArray) -> Unit, command: BleCommand) {
        pendingCommandSendChannel.value = {
            try {
                writer(command.toByteArray())
            } catch (e: Exception) {
                loge("Error writing to gatt", e)
            }
        }
    }

    fun retry()  = stateFlow.restart()

    @OptIn(DelicateCoroutinesApi::class)
    private fun Peripheral.disconnectSafely() {
        // VM scope is not available during closing -> GlobalScope
        GlobalScope.launch {
            withTimeout(10000) {
                disconnect()
            }
        }
    }

    private suspend fun Peripheral.connectSafely() {
        withTimeout(10000) {
            connect()
        }
    }
}


sealed interface MainUiState {
    @Immutable
    data class MissingPermissions(
        val permissions: List<String>,
        val reevaluatePermissions: () -> Unit,
    ): MainUiState

    data object PermissionsGranted: MainUiState

    @Immutable
    data class RequestAccess(
        val onClickRequest: () -> Unit,
    ): MainUiState

    @Immutable
    data class Progress(
        val message: String,
    ): MainUiState

    @Immutable
    data class Ready(
        val initialModeIndex: Int,
        val modes: List<LedMode>,
        val initialBrightnessValue: Float,
        val onBrightnessChange: (Float) -> Unit,
        val initialSpeedValue: Float,
        val onSpeedChange: (Float) -> Unit,
        val onClickRefresh: () -> Unit,
    ): MainUiState

    data object Error: MainUiState
}

