package de.labinger.holding.ble.command

import androidx.compose.runtime.Immutable

@Immutable
data class LedMode(
    val name: String,
    val onSelect: () -> Unit,
)

fun createLedModes(send: (mode: Int) -> Unit) = listOf(
    LedMode(
        name = "Global",
        onSelect = { send(BleCommand.GLOBAL) }
    ),
    LedMode(
        name = "Asteroid",
        onSelect = { send(BleCommand.ASTEROID) }
    ),
    LedMode(
        name = "Pulsar",
        onSelect = { send(BleCommand.PULSAR) }
    ),
    LedMode(
        name = "Glitter",
        onSelect = { send(BleCommand.GLITTER) }
    ),
)