package de.labinger.holding.ble

import com.benasher44.uuid.Uuid
import com.juul.kable.Filter
import com.juul.kable.Options
import com.juul.kable.requestPeripheral
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.await
import kotlinx.coroutines.flow.Flow
import kotlinx.coroutines.flow.MutableSharedFlow
import kotlinx.coroutines.flow.first
import kotlinx.coroutines.flow.flow

actual fun createBlePeripheral(
    scope: CoroutineScope,
    name: String,
    serviceUuids: List<Uuid>
): Flow<BleFinderResult>? {
    return flow {
        val clickFlow = MutableSharedFlow<Unit>(extraBufferCapacity = 1)
        emit(BleFinderResult.Request { clickFlow.tryEmit(Unit) })
        clickFlow.first()


        emit(BleFinderResult.Progress("Request peripheral access"))
        val options = Options(
            filters = listOf(Filter.Name.Exact(name)),
            optionalServices = serviceUuids,
        )
        try {
            val peripheral = scope.requestPeripheral(options).await()
            emit(BleFinderResult.Success(peripheral))
        } catch (e: Exception) {
            console.error(e.stackTraceToString())
            throw e
        }
    }
}