package de.labinger.holding.ble.command


class BleCommand (private val rawCommand: Int) {
    companion object {
        const val TYPE_MASK: Int = 0xF000000
        const val PAYLOAD_MASK =  0x0FFFFFF

        // Types
        private const val SPEED_TYPE = 0x1000000
        private const val BRIGHTNESS_TYPE = 0x2000000
        private const val MODE_TYPE = 0x3000000
        private const val COLOR_TYPE = 0x4000000

        // Modes
        const val GLOBAL = 0x0000001
        const val PULSAR = 0x0000002
        const val ASTEROID = 0x0000003
        const val GLITTER = 0x0000004

        fun createModeCommand(mode: Int) = BleCommand(MODE_TYPE or mode)

        fun createSpeedCommand(speed: UByte) = BleCommand(SPEED_TYPE or speed.toInt())

        fun createBrightnessCommand(brightness: UByte) = BleCommand(BRIGHTNESS_TYPE or brightness.toInt())

        fun createColorCommand(color: Int) = BleCommand(COLOR_TYPE or (PAYLOAD_MASK and color))
    }

    val payload get() =  rawCommand and PAYLOAD_MASK

    /**
     * @returns mode
     */
    val action get() =  rawCommand and TYPE_MASK

    val isModeCommand get() =  rawCommand and TYPE_MASK == MODE_TYPE
    val isModeGlobal get() =  rawCommand and PAYLOAD_MASK == GLOBAL
    val isModeAsteroid get() =  rawCommand and PAYLOAD_MASK == ASTEROID
    val isModePulsar get() =  rawCommand and PAYLOAD_MASK == PULSAR
    val isModeGlitter get() =  rawCommand and PAYLOAD_MASK == GLITTER

    val isSpeedCommand get() =  rawCommand and TYPE_MASK == SPEED_TYPE
    val isBrightnessCommand get() =  rawCommand and TYPE_MASK == BRIGHTNESS_TYPE
    val isColorCommand get() =  rawCommand and TYPE_MASK == COLOR_TYPE

    fun toByteArray(): ByteArray = byteArrayOf(
        (rawCommand shr 24).toByte(),
        (rawCommand shr 16).toByte(),
        (rawCommand shr 8).toByte(),
        rawCommand.toByte()
    )
}
