package de.labinger.holding.ble

import com.benasher44.uuid.Uuid
import com.juul.kable.Peripheral
import com.juul.kable.Scanner
import com.juul.kable.peripheral
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.flow.*

fun findBlePeripheral(
    scope: CoroutineScope,
    name: String,
    serviceUuids: List<Uuid>,
): Flow<BleFinderResult> {
    return createBlePeripheral(scope, name, serviceUuids) ?: findBlePeripheralByScan(scope, name, serviceUuids)
}

/**
 * Only implemented for JS
 */
expect fun createBlePeripheral(
    scope: CoroutineScope,
    name: String,
    serviceUuids: List<Uuid>,
): Flow<BleFinderResult>?

/**
 * Only supported by some browsers
 */
private fun findBlePeripheralByScan(
    scope: CoroutineScope,
    name: String,
    @Suppress("UNUSED_PARAMETER") // needed in JS implementation
    serviceUuids: List<Uuid>,
) = flow {
    val clickFlow = MutableSharedFlow<Unit>(extraBufferCapacity = 1)
    emit(BleFinderResult.Request { clickFlow.tryEmit(Unit) })
    clickFlow.first()
    val advertisement = Scanner {}.advertisements.onEach {
        emit(BleFinderResult.Progress("Scanned: " + it.name))
    }.firstOrNull { it.name == name } ?: error("Could not find mask BLE advertisement")

    val peripheral = scope.peripheral(advertisement)
    emit(BleFinderResult.Success(peripheral))
}

sealed interface BleFinderResult {
    data class Request(val onClick: () -> Unit): BleFinderResult
    data class Progress(val message: String): BleFinderResult
    data class Success(val peripheral: Peripheral): BleFinderResult
}
